import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DomConfirmDialogComponent } from './dom-confirm-dialog.component';
import { MatButtonModule, MatDialogModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';

@NgModule({
  imports: [
    CommonModule,
    MatDialogModule,
    MatButtonModule,
    FlexLayoutModule
  ],
  declarations: [DomConfirmDialogComponent],
  exports: [DomConfirmDialogComponent],
  entryComponents: [DomConfirmDialogComponent]
})
export class DomConfirmDialogModule { }



