import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InteractionComponent } from './interaction.component';
import { interactionRouting } from './interaction.routing';
import { PhotoGridModule } from './photo-grid/photo-grid.module';
import { MatTabsModule, MatIconModule, MatButtonModule } from '@angular/material';
import { DomFlexContainerModule } from '@app/util/dom-flex-container/dom-flex-container.module';
import { DomSnackBarModule } from '@app/util/dom-snack-bar/dom-snack-bar.module';
import { DomApiModule } from '@app/util/dom-api/dom-api.module';
import { InteractionService } from '@app/project/interaction/interaction.service';

@NgModule({
  imports: [
    CommonModule,
    MatTabsModule,
    MatIconModule,
    MatButtonModule,
    DomFlexContainerModule,
    DomSnackBarModule,
    DomApiModule,
    interactionRouting,
    PhotoGridModule
  ],
  declarations: [InteractionComponent],
  providers: [InteractionService]
})
export class InteractionModule { }



