import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { ImageModel } from '@app/model/image.model';

@Component({
  selector: 'app-photo-grid',
  templateUrl: './photo-grid.component.html',
  styleUrls: ['./photo-grid.component.css']
})
export class PhotoGridComponent implements OnInit {
  @Input() imagesList: string[];
  @Input() boardId: string;

  @Output() imageRemoved: EventEmitter<ImageModel>;
  @Output() imageCreated: EventEmitter<{ boardId: string; image: any }>;
  @Output() imageUpdated: EventEmitter<ImageModel>;

  constructor() {
    this.imageRemoved = new EventEmitter();
    this.imageCreated = new EventEmitter();
    this.imageUpdated = new EventEmitter();
  }

  ngOnInit() {}

  public anySelectorRemoved(event) {
    this.imageRemoved.emit(event);
  }

  public anySelectorUpdated(event) {
    this.imageUpdated.emit(event);
  }

  public anySelectorCreated(event) {
    this.imageCreated.emit({ boardId: this.boardId, image: event });
  }
}



