import { RouterModule, Routes } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { InteractionComponent } from './interaction.component';

const interactionRoutes: Routes = [
    { path: '', component: InteractionComponent }
];

export const interactionRouting: ModuleWithProviders = RouterModule.forChild(interactionRoutes);



