import {
  Component,
  OnInit,
  Input,
  AfterViewInit,
  Output,
  EventEmitter
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { DomConfirmDialogComponent } from '@app/util/dom-confirm-dialog/dom-confirm-dialog.component';
import { MatDialog } from '@angular/material';
import { DomConfirmDialogActions } from '@app/util/dom-confirm-dialog/dom-confirm-dialog.constants';
import { ImageModel } from '@app/model/image.model';
import { ReadOnlyUtil } from '@app/util/read-only-util';

@Component({
  selector: 'app-photo-selector',
  templateUrl: './photo-selector.component.html',
  styleUrls: ['./photo-selector.component.css']
})
export class PhotoSelectorComponent implements OnInit, AfterViewInit {
  @Input() imageModel: ImageModel;
  public imageSource: String;
  protected sanitizedImage: any;
  protected photoTitle = 'INSIRA O TÍTULO DA FOTO';
  protected editingTitle = false;

  @Output() removeImage: EventEmitter<ImageModel>;
  @Output() createImage: EventEmitter<any>;
  @Output() updateImage: EventEmitter<ImageModel>;

  constructor(public sanitizer: DomSanitizer, public dialog: MatDialog) {
    this.removeImage = new EventEmitter();
    this.createImage = new EventEmitter();
    this.updateImage = new EventEmitter();
  }

  ngOnInit() {
    if (this.imageModel && !this.sanitizedImage) {
      this.sanitizedImage = this.sanitizer.bypassSecurityTrustStyle(
        `url(${ReadOnlyUtil.RequestPaths.SERVER_FILES}/${this.imageModel.path})`
      );

      this.imageSource = `${ReadOnlyUtil.RequestPaths.SERVER_FILES}/${
        this.imageModel.path
      }`;
    }
  }

  ngAfterViewInit(): void {}

  public startEditPhotoTitle(): void {
    this.editingTitle = true;
  }

  public confirmPhotoTitle(): void {
    this.editingTitle = false;
    this.updateImage.emit(this.imageModel);
  }

  public uploadPhoto(files): void {
    const reader = new FileReader();
    console.log(files);
    reader.readAsDataURL(files.target.files[0]);

    reader.addEventListener('load', () => {
      this.imageSource = reader.result;

      const image = files.target.files[0];

      const formData = new FormData();
      formData.append('title', image.name);
      formData.append('file_type', image.type);
      formData.append('file', image);

      this.createImage.emit(formData);

      this.imageModel = new ImageModel();
      this.imageModel.title = image.name;
    });

    reader.onerror = function(error) {
      console.log('Error: ', error);
    };
  }

  public btnRemoveClick() {
    const dialogRef = this.dialog.open(DomConfirmDialogComponent, {
      width: '320px',
      data: {
        title: 'Remover Imagem',
        message: `Deseja remover ${this.photoTitle}?`
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result === DomConfirmDialogActions.CONFIRM) {
        this.removeImage.emit(this.imageModel);
      }
    });
  }

  public wrapClick() {
    document.getElementById('uploadPicture').click();
  }
}



