import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { DomConfirmDialogActions } from '@app/util/dom-confirm-dialog/dom-confirm-dialog.constants';

@Component({
  selector: 'app-dom-confirm-dialog',
  templateUrl: './dom-confirm-dialog.component.html',
  styleUrls: ['./dom-confirm-dialog.component.css']
})
export class DomConfirmDialogComponent implements OnInit {
  public dialogMessage = '';
  public dialogTitle = '';

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<DomConfirmDialogComponent>
  ) {}

  ngOnInit() {
    this.dialogMessage = this.data.message;
    this.dialogTitle = this.data.title;
  }

  public confirmClick() {
    this.dialogRef.close(DomConfirmDialogActions.CONFIRM);
  }

  public cancelClick() {
    this.dialogRef.close(DomConfirmDialogActions.CANCEL);
  }
}



