import { Injectable } from '@angular/core';
import { DomApiService } from '@app/util/dom-api/dom-api.service';
import { ReadOnlyUtil } from '@app/util/read-only-util';
import { ImageModel } from '@app/model/image.model';
import { ImageParser } from '@app/model/parser/image.parser';

@Injectable()
export class InteractionService {
  constructor(protected domApi: DomApiService) {}

  public getPhotoBoardByProject(projectId: string) {
    return this.domApi.get(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.PHOTOBOARD
      }/project/${projectId}`
    );
  }

  public addImageToPhotoBoard(photoBoardId: string, imageData: any) {
    return this.domApi.postMultiPart(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.PHOTOBOARD
      }/add-image-to/${photoBoardId}`,
      imageData
    );
  }

  public updateImageTitle(image: ImageModel) {
    const body = { title: image.title };
    return this.domApi.patch(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.IMAGE
      }/${image.id}`,
      body
    );
  }

  public deleteImage(image: ImageModel) {
    return this.domApi.delete(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.IMAGE
      }/${image.id}`
    );
  }
}



