import { PhotoBoardModel } from '@app/model/photo-board.model';
import { ImageParser } from '@app/model/parser/image.parser';

export class PhotoBoardParser {
  public static parse(json: any): PhotoBoardModel {
    const obj = new PhotoBoardModel();

    if (json) {
      obj.id = json.id ? json.id : '';
      obj.title = json.title ? json.title : '';
      obj.description = json.description ? json.description : '';
      obj.createdAt = json.createdAt || '';
      obj.updatedAt = json.updatedAt || '';
      obj.images = [];

      if (json.images) {
        json.images.forEach(image => {
          obj.images.push(ImageParser.parse(image));
        });
      }
    } else {
      throw new Error('PhotoBoard - Não foi possível realizar a conversão.');
    }

    return obj;
  }

  public static parseToJson(object: PhotoBoardModel): any {
    const json = {};

    if (json) {
      json['id'] = object.id;
      json['description'] = object.description;
      json['createdAt'] = object.createdAt;
      json['updatedAt'] = object.updatedAt;
    } else {
      throw new Error(
        'PhotoBoard - Não foi possível realizar a conversão JSON.'
      );
    }

    return json;
  }
}



