import { Component, OnInit, AfterViewInit } from '@angular/core';
import { DomSnackBarService } from '@app/util/dom-snack-bar/dom-snack-bar.service';
import { InteractionService } from '@app/project/interaction/interaction.service';
import { ImageModel } from '@app/model/image.model';
import { ImageParser } from '@app/model/parser/image.parser';
import { PhotoBoardModel } from '@app/model/photo-board.model';
import { PhotoBoardParser } from '@app/model/parser/photo-board.parser';

@Component({
  selector: 'app-interaction',
  templateUrl: './interaction.component.html',
  styleUrls: ['./interaction.component.css']
})
export class InteractionComponent implements OnInit, AfterViewInit {
  constructor(
    public snackBarService: DomSnackBarService,
    public interactionService: InteractionService
  ) {}

  public quadroI = new Array<ImageModel>(12);

  public quadroII = new Array<ImageModel>(12);

  public PhotoBoardA: PhotoBoardModel;
  public PhotoBoardB: PhotoBoardModel;

  ngOnInit() {}

  ngAfterViewInit(): void {
    this.interactionService
      .getPhotoBoardByProject('a8246dd4-a79a-4d37-8b41-7c1342167e89')
      .subscribe(boards => {
        if (boards && boards.length === 2) {
          this.PhotoBoardA = PhotoBoardParser.parse(boards[0]);
          this.PhotoBoardB = PhotoBoardParser.parse(boards[1]);
          let index = 0;
          this.PhotoBoardA.images.forEach(image => {
            this.quadroI[index] = ImageParser.parse(image);
            index++;
          });
          index = 0;
          this.PhotoBoardB.images.forEach(image => {
            this.quadroII[index] = ImageParser.parse(image);
            index++;
          });
        }
      });
  }

  public createImage(event) {
    this.interactionService.addImageToPhotoBoard(event.boardId, event.image);
  }

  public updateImageTitle(event) {
    this.interactionService.updateImageTitle(event as ImageModel);
  }

  public deleteImage(event) {
    this.interactionService.deleteImage(event).subscribe(
      success => {
        this.snackBarService.snackBarSuccess('Imagem Removida!');
      },
      error => {
        this.snackBarService.snackBarError(
          'Erro ao remover Imagem. Tente novamente.'
        );
      }
    );
  }

  public savePhotoBoard() {
    this.snackBarService.snackBarSuccess('Quadro de Imagens Salvo!');
  }
}



