import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PhotoSelectorComponent } from './photo-selector.component';
import {
  MatIconModule,
  MatCardModule,
  MatButtonModule,
  MatTooltipModule,
  MatDialogModule,
  MatInputModule
} from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule } from '@angular/forms';
import { DomConfirmDialogModule } from '@app/util/dom-confirm-dialog/dom-confirm-dialog.module';

@NgModule({
  imports: [
    CommonModule,
    MatIconModule,
    MatCardModule,
    MatTooltipModule,
    MatButtonModule,
    MatInputModule,
    MatDialogModule,
    FormsModule,
    FlexLayoutModule,
    DomConfirmDialogModule
  ],
  declarations: [PhotoSelectorComponent],
  exports: [PhotoSelectorComponent]
})
export class PhotoSelectorModule {}



